* {
	margin: 0px;
	padding: 0px;
	font-size: 15px;
}

body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

div#root {
	box-sizing: border-box;
	min-width: 100vw;
	min-height: 100vh;
	background-color: #D0D0D0;

	display: flex;
	align-items: center; //Centers vertically
	justify-content: center; //Centers horizontally

	overflow: hidden;
	overflow-y: scroll;
}